import React, { useState } from "react";
import Pagina from "./pages/pagina/Pagina.js";
import Mapa from "./pages/mapa/Mapa.js";
import Login from "./pages/login/Login.js";
import Admin from "./pages/admin/Admin.js";
import CadastroPonto from './pages/admin/Ponto/CadastroPonto';
import CadastroCliente from './pages/admin/CadastroCliente';
import CadastroPagina from './pages/admin/CadastroPagina';
import { BrowserRouter, Route } from "react-router-dom";
import { useHistory } from "react-router"
import ListaPontos from "./pages/admin/Ponto/ListaPontos.js";
import EditarPonto from "./pages/admin/Ponto/EditarPonto.js";
import Tela from "./pages/admin/Tela.js";

document.getElementById("loader").style.display = "none";

export default function () {
  const [id_pagina, setId_pagina] = useState();
  const [cria_ponto, setcria_ponto] = useState();
  const [perfil_usuario, setperfil_usuario] = useState();

  function trocaTela(element) {
    setId_pagina(element);
  }

  function mapa(history) {
    if (!id_pagina)
      return <Mapa
        trocaTela={trocaTela}
        perfil_usuario={perfil_usuario}
        cria_ponto={cria_ponto} 
        history={history}
        />;
  }

  function pagina() {
    if (id_pagina)
      return <Pagina id_pagina={id_pagina} trocaTela={trocaTela} />;
  }

  function CoreApp() {
    const history = useHistory()
    return (<>
      {mapa(history)}
      {pagina()}
    </>)
  }


  function funcaoSelecionaPerfil(k) {
    setcria_ponto(k.criaPonto)
    setperfil_usuario(k.perfilUsuario)
  }

  return (
    <BrowserRouter
      basename="/app"
      className='container'
    >
      
      <Route exact render={(props => <CoreApp {...props} cria_ponto={cria_ponto}/>)} path="/" />
      <Route exact render={(props => <Login {...props} perfil={funcaoSelecionaPerfil} />)} path="/Login" />

      <Route exact render={(props => <Admin {...props}
        perfil_usuario={perfil_usuario}
        cria_ponto={cria_ponto} />)} path="/Admin" />


      <Route exact render={(props => <CadastroPonto {...props} />)} path="/CadastroPonto" />
      <Route exact render={(props => <CadastroCliente {...props} />)} path="/CadastroCliente" />
      <Route exact render={(props => <CadastroPagina {...props} />)} path="/CadastroPagina" />
      <Route exact render={(props => <ListaPontos {...props} />)} path="/ListaPontos" />
      <Route exact render={(props => <Tela {...props}  />)} path="/Tela/:id" />
      <Route exact render={(props => <EditarPonto {...props}  />)} path="/EditarPonto/:id" />
    </BrowserRouter>
  );
};
