import React, { Component } from 'react';


const GetPaginasCadastro = (props) =>
    fetch(`${process.env['REACT_APP_BASE_URL']}/CadastroPagina/LoadPaginaCadastro`, {
        method: 'get',
        headers: new Headers({
            'content-type': 'application/json',
            'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
        })
    }).then(x => {
        if (x.status === 200) return x.json()
        if (x.status === 401) props.history.push('/Login')
        return
    })


const PutPagina = (pagina) =>
    fetch(`${process.env['REACT_APP_BASE_URL']}/CadastroPagina/PutPagina`, {
        method: 'put',
        body: JSON.stringify(pagina),
        headers: new Headers({
            'content-type': 'application/json',
            'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
        })
    }).then(x => {
        if (x.status === 200) return x.json()
        return
    })


const GetPagina = (pagina) =>
    fetch(`${process.env['REACT_APP_BASE_URL']}/CadastroPagina/GetPagina?id=${pagina}`, {
        method: 'get',
        headers: new Headers({
            'content-type': 'application/json',
            'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
        })
    }).then(x => {
        if (x.status === 200) return x.json()
        return
    })


const PostPaginaCadastro = (pagina) =>
    fetch(`${process.env['REACT_APP_BASE_URL']}/CadastroPagina/PostPagina`, {
        method: 'post',
        body: JSON.stringify(pagina),
        headers: new Headers({
            'content-type': 'application/json',
            'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
        })
    }).then(x => x.json())



const DeletePagina = (pagina) =>
    fetch(`${process.env['REACT_APP_BASE_URL']}/CadastroPagina/DeletePagina?id=${pagina}`, {
        method: 'delete',
        body: JSON.stringify(pagina),
        headers: new Headers({
            'content-type': 'application/json',
            'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
        })
    }).then(x => {
        if (x.status === 200) return x.json()
        return
    })


class CadastroPagina extends Component {
    state = {
        concessionarias: [],
        paginas: [],
        sites: [],
        contatos: [],
        cadastra: false,
        escolhido: null
    }



    RequisitaLoad() {
        GetPaginasCadastro(this.props).then(x => {
            if (x != undefined) {
                const { concessionarias, paginas, sites, contatos } = x
                this.setState({
                    concessionarias,
                    paginas,
                    sites,
                    contatos
                });
            }
        })
    }


    Cadastra() {

        if (this.state.cadastra) {
            return (<form onSubmit={(event) => {

                event.preventDefault();


                if (this.state.escolhido) {

                    const PaginaSites = []


                    var slides = document.getElementsByClassName('sites_');
                    for (var i = 0; i < slides.length; i++) {
                        PaginaSites.push({ paginaId: document.getElementById('IdPagina').value, siteId: slides.item(i).innerHTML });
                    }



                    const payload = {
                        IdPagina: document.getElementById('IdPagina').value,
                        NomePagina: document.getElementById('NomePagina').value,
                        EnderecoPagina: document.getElementById('EnderecoPagina').value,
                        ConcessionariaId: document.getElementById('ConcessionariaId').value,
                        PaginaSites
                    }

                    PutPagina(payload).then(data => console.log(data))




                } else {

                    // const PaginaSites = 


                    // console.log(document.getElementsByClassName('lists'))


                    // return


                    const payload = {
                        NomePagina: document.getElementById('NomePagina').value,
                        EnderecoPagina: document.getElementById('EnderecoPagina').value,
                        ConcessionariaId: document.getElementById('ConcessionariaId').value
                    }



                    const retorno = x => {
                        console.log(x)
                        document.getElementById('IdPagina').value = ""
                        document.getElementById('NomePagina').value = ""
                        document.getElementById("EnderecoPagina").value = ""
                        document.getElementById("ConcessionariaId").value = ""



                        this.RequisitaLoad()
                    }
                    PostPaginaCadastro(payload)
                        .then(retorno)




                }

            }}>

                <input type="text" id="IdPagina" placeholder="IdPagina" hidden />
                <input type="text" id="NomePagina" placeholder="NomePagina" />
                <input type="text" id="EnderecoPagina" placeholder="EnderecoPagina" />
                <select id="ConcessionariaId" onChange={data => console.log(data.target.selectedIndex)}>
                    {this.state.concessionarias.map(data => {
                        return <option
                            key={data.idConcessionaria}
                            value={data.idConcessionaria}>
                            {data.idConcessionaria} -- {data.nomeConcessionaria}
                        </option>
                    })}
                </select>

                <div id='lists'>

                </div>

                <button type="submit">ENVIO</button>
                <button onClick={() => this.setState({ cadastra: false, escolhido: null })}>CANCELO</button>
            </form>)
        } else {
            return (<div>
                <button onClick={() => this.setState({ cadastra: true })}>teste</button>

                {this.state.paginas.map(x => <div
                    key={x.idPagina}>
                    <span>
                        {x.idPagina} -- {x.nomePagina}</span>
                    <button onClick={() => {
                        this.setState({ cadastra: true, escolhido: x.idPagina })

                        GetPagina(x.idPagina).then(data => {
                            console.log(data)

                            document.getElementById('IdPagina').value = data.idPagina
                            document.getElementById('NomePagina').value = data.nomePagina
                            document.getElementById("EnderecoPagina").value = data.enderecoPagina
                            document.getElementById("ConcessionariaId").value = data.concessionariaId

                            data.paginaSites.forEach(x => {
                                // console.log(x)
                                const span = document.createElement('span')
                                span.innerHTML = x.siteId
                                span.className = "sites_"
                                const br = document.createElement('br')
                                document.getElementById("lists").appendChild(span);
                                document.getElementById("lists").appendChild(br);
                            })
                        })
                    }}>
                        EDITAR
                    </button>

                    <button
                        onClick={() => DeletePagina(x.idPagina).then(data => console.log(data))}>Deletar</button>

                    <br />
                </div>)}
            </div>)
        }
    }


    componentDidMount() {
        this.RequisitaLoad()
    }

    render() {

        return <div style={{ backgroundColor: 'white' }}>
            {this.Cadastra()}
        </div>;
    }
}

export default CadastroPagina;