import React from "react";
import loadGoogleMapsApi from "load-google-maps-api";
import { Link } from "react-router-dom";

import "./menu.css"
import "./mapa.css";




 const GetPontos = (latLong, history) => fetch(`${process.env['REACT_APP_BASE_URL']}/Ponto/GetPontos`, {
  method: 'post',
  body: JSON.stringify(latLong),
  headers: new Headers({
    'content-type': 'application/json',
    'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
  })
})
.then(x => {
  if (x.status === 200) return x.json()
  if (x.status === 401) history.push("/Login")
  return
})







class getUrlVal {
  lista = null;
  constructor(parametros_url) { 
    let lista_parametros_url_resolvidas = {};
    const get = (name) => {
      if (
        (name = new RegExp("[?&]" + encodeURIComponent(name) + "=([^&]*)").exec(
          window.location.search
        ))
      )
        return decodeURIComponent(name[1]);
    };

    parametros_url.forEach((element) => {
      lista_parametros_url_resolvidas[element] = get(element);
    });
    this.lista = lista_parametros_url_resolvidas;
  }

  get_list() {
    return this.lista;
  }
}




var markes = []





export default (props) => {
  let { trocaTela } = props;

  const perfil_usuario = sessionStorage.getItem("typeUser")

  document.title = `BAHM - MAPA`;

  window.addEventListener('resize', function () {
    const mapa = document.getElementById('map')
    if (mapa)
      mapa.style.height = window.innerHeight + 'px'
  });


  if (
    !localStorage.getItem("lat") ||
    !localStorage.getItem("long") ||
    !localStorage.getItem("zoom")
  ) {
    localStorage.setItem("lat", "-21");
    localStorage.setItem("long", "-50");
    localStorage.setItem("zoom", "9");
  }




  loadGoogleMapsApi({ key: "AIzaSyBZkRztzytJboga-ppS__Lb8YbettZs8Rk" })
    .then(carregaMapa(props.history));



  function carregaMapa(history) {
    let _latMax, _latMin, _longMax, _longMin;

    function okok(googleMaps) {
      let map;



      const latLongZoom = new getUrlVal(["lat", "long", "zooml"]);
      let { lat, long, zooml } = latLongZoom.get_list();

      let latitude;
      let longitude;
      let zoom;

      if ((lat, long, zooml)) {
        latitude = lat;
        longitude = long;
        zoom = Number(zooml);
        window.location.search = "";
      } else {
        latitude = localStorage.getItem("lat");
        longitude = localStorage.getItem("long");
        zoom = Number(localStorage.getItem("zoom"));
      }


      const mapDom = document.getElementById("map");
      const center = new googleMaps.LatLng(parseFloat(latitude), parseFloat(longitude));
      const mapTypeId = googleMaps.MapTypeId.HYBRID;
      map = new googleMaps.Map(mapDom, {
        center,
        zoom,
        mapTypeId,
      });

      MontaDados();

      map.addListener('zoom_changed', MontaDados)

      map.addListener("drag", MontaDados);


      
      
      function centerMap(zoom, dist, map) {

        

        
        let latMax;
        let latMin;
        let longMax;
        let longMin;
        

        let retorno_dados

        if (map.getZoom() >= zoom) {
          latMax = Math.round(map.getCenter().lat() - dist);
          latMin = Math.ceil(map.getCenter().lat() + dist);
          longMax = Math.round(map.getCenter().lng() - dist);
          longMin = Math.ceil(map.getCenter().lng() + dist);          

          retorno_dados =
            _latMax !== latMax ||
            _latMin !== latMin ||
            _longMax !== longMax ||
            _longMin !== longMin;

          if (retorno_dados) {
            _latMax = latMax;
            _latMin = latMin;
            _longMax = longMax;
            _longMin = longMin;
          }

        }

        if (retorno_dados)
          return {
            Latitude: map.getCenter().lat(),
            Longitude: map.getCenter().lng()
          };
        else return null;
      }





      async function MontaDados() {
        localStorage.setItem("lat", map.getCenter().lat());
        localStorage.setItem("long", map.getCenter().lng());
        localStorage.setItem("zoom", map.getZoom());
        if (document.getElementById("lat"))
          document.getElementById("lat").value = map.getCenter().lat().toFixed(6);
        if (document.getElementById("long"))
          document.getElementById("long").value = map.getCenter().lng().toFixed(6);
        if (document.getElementById("zoom"))
          document.getElementById("zoom").value = map.getZoom();
        const link = `${window.location}?lat=${map.getCenter().lat()}&long=${map.getCenter().lng()}&zooml=${map.getZoom()}`;
        if (document.getElementById("link")) document.getElementById("link").value = link

        const centro = centerMap(8, 2, map);

        if (!centro) return;
        // console.log(centro)

        const { Latitude, Longitude } = centro;

        const retornoApiPontos = await GetPontos({ Latitude, Longitude, Zoom: 8 }, history)
   

        
        if(!retornoApiPontos) return 
        // console.log(retornoApiPontos)
        if(!retornoApiPontos.pontos) return 
        
        // console.log(retornoApiPontos)
        
        const dados = retornoApiPontos.pontos

        try {
          dados.forEach((element) => {
            const { latitude, longitude, link } = element;
            const position = new googleMaps.LatLng(
              parseFloat(latitude),
              parseFloat(longitude)
            );

            let icon


            if (link.indexOf('http') === -1) {
              icon = `${process.env['REACT_APP_BASE_URL2']}${link}`
            } else {
              icon = link;
            }


            const b = new googleMaps.Marker({
              map,
              position,
              icon,
            });

            markes.push({
              b,
              c: b.position.lat(),
              d: b.position.lng()
            });


            const div = document.createElement("div");
            const text = document.createElement("text");
            text.innerHTML = element.nome_ponto

            div.appendChild(text);

            if (element.tipo_icone !== "KM") text.style.cursor = 'pointer';


            text.addEventListener('click', function () {
              if (element.tipo_icone === "NÃO") return;
              if (element.pagina !== 'false') trocaTela(element.pagina)
              else return
            })

            const infoWindow = new googleMaps.InfoWindow();
            b.addListener("click", function () {
              // markes.forEach(x=>{x.b.setMap(null)})
              infoWindow.setContent(div);
              infoWindow.open(this.mapa, b);
            });
          });
        } catch (error) {
          console.log("formato array incorreto");
        }

      }





      if (document.getElementById("control")) {
        document.getElementById("control").addEventListener("click", botaoApertado);
      }


      function botaoApertado() {
        // var latLng = markerInstancia.latLong(
        //   document.getElementById("lat").value,
        //   document.getElementById("long").value
        // );
    //     // map.panTo(latLng);
    //     // map.setZoom(Number(document.getElementById("zoom").value));
        MontaDados();
      }
    }

    return okok
  }



  return (

    <>
      <div id="myNav" className="overlay" style={{
        zIndex: 12
      }}>
        <button className="closebtn"
          onClick={() => document.getElementById("myNav").style.width = "0%"}>&times;</button>
        <div className="overlay-content">
          Aqui é dentro do menu
        </div>
      </div>

      <span onClick={() => document.getElementById("myNav").style.width = "100%"} style={{
        fontSize: 30,
        cursor: 'pointer',
        position: 'absolute',
        zIndex: 10,
        marginTop: 80,
        marginLeft: 10,
        backgroundColor: 'white',
        display: "none"
      }}>&#9776; MENU</span>




      <input maxLength="10" size="8" className="item" type="text" id="lat" placeholder="latitude" style={{
        fontSize: 20,
        cursor: 'pointer',
        position: 'absolute',
        zIndex: 10,
        marginTop: 130,
        marginLeft: 10,
        backgroundColor: 'white',
        padding: 2
      }} />

      <input maxLength="10" size="8" className="item" type="text" id="long" placeholder="longitude" style={{
        fontSize: 20,
        cursor: 'pointer',
        position: 'absolute',
        zIndex: 10,
        marginTop: 180,
        marginLeft: 10,
        backgroundColor: 'white',
        padding: 2
      }} />

      <input maxLength="2" size="2" className="item" type="text" id="zoom" placeholder="zoom" style={{
        fontSize: 20,
        cursor: 'pointer',
        position: 'absolute',
        zIndex: 10,
        marginTop: 230,
        marginLeft: 10,
        backgroundColor: 'white',
        padding: 2
      }} />

      <textarea className="item" type="text" id="link" placeholder="link" style={{
        fontSize: 20,
        cursor: 'pointer',
        position: 'absolute',
        zIndex: 10,
        marginTop: 280,
        marginLeft: 10,
        backgroundColor: 'white',
        padding: 2,
        display: 'none'
      }} ></textarea>


      <button style={{
        fontSize: 20,
        cursor: 'pointer',
        position: 'absolute',
        zIndex: 10,
        marginTop: 280,
        marginLeft: 10,
        backgroundColor: 'white',
        padding: 2
      }} className="item" id="control">Ir para posição</button>




      <button className="copy" onClick={() => {
        const btnCopy = document.querySelector('button.copy');
        const textArea = document.querySelector('textarea');

        btnCopy.addEventListener('click', (e) => {
          e.preventDefault();

          navigator.clipboard.writeText(textArea.value);

          alert(textArea.value);
        });
      }} style={{
        fontSize: 20,
        cursor: 'pointer',
        position: 'absolute',
        zIndex: 10,
        marginTop: 330,
        marginLeft: 10,
        backgroundColor: 'white',
        padding: 2
      }}>Copia do link</button >


      <Link to="/Admin" style={{
        fontSize: 20,
        cursor: 'pointer',
        position: 'absolute',
        zIndex: 10,
        marginTop: 380,
        marginLeft: 10,
        backgroundColor: 'white',
        padding: 2,
        display: perfil_usuario === "ADMINISTRADOR" ? "block" : "none"
      }}
      >

        <button style={{
          backgroundColor: 'gray',
          color: 'white',
          padding: 10,
          fontWeight: 'bold'
        }}>
          ADMIN
        </button>
      </Link>


      <div id="map" style={{ height: window.innerHeight }} />
    </>
  );
};
