import React, { Component } from 'react';
import { Link } from "react-router-dom";


class Admin extends Component {
  render() {
    return (<div className='container-fluid' style={{ backgroundColor: 'white', height: window.innerHeight }}>

      <Link to="/ListaPontos">

        <button style={{
          backgroundColor: 'gray',
          color: 'white',
          padding: 10,
          fontWeight: 'bold'
        }}>
          Cadastra Ponto
        </button>
      </Link>

      <Link to="/CadastroCliente">

        <button style={{
          backgroundColor: 'gray',
          color: 'white',
          padding: 10,
          fontWeight: 'bold'
        }}>
          Cadastra Cliente
        </button>
      </Link>


      <Link to="/CadastroPagina">

        <button style={{
          backgroundColor: 'gray',
          color: 'white',
          padding: 10,
          fontWeight: 'bold'
        }}>
          Cadastra Pagina
        </button>
      </Link>

    </div>)
  }
}

export default Admin;



