import React, { Component } from 'react';
import { Link } from "react-router-dom";



const GetPontoCadastro = (props) => {


    return fetch(`${process.env['REACT_APP_BASE_URL']}/CadastroPonto/LoadPontoCadastro`, {
        method: 'get',
        headers: new Headers({
            'content-type': 'application/json',
            'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
        })
    }).then(x => {
        if (x != undefined) {
            if (x.status === 200) return x.json()
            if (x.status === 401) props.history.push('/Login')
        }
        return
    })

}

const PostPontoCadastro = (ponto, props) =>
    fetch(`${process.env['REACT_APP_BASE_URL']}/CadastroPonto/PostPonto`, {
        method: 'post',
        body: JSON.stringify(ponto),
        headers: new Headers({
            'content-type': 'application/json',
            'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
        })
    }).then(x => {
        props.history.push('/ListaPontos')
        return x.json()
    })




class CadastroPonto extends Component {
    state = {
        pontos: [],
        icones: [],
        paginas: []
    }

    envia(arr) {

        PostPontoCadastro(arr, this.props)
            .then(x => {



                GetPontoCadastro().then(x => {
                    const { pontos, icones, paginas } = x
                    this.setState({
                        pontos,
                        icones,
                        paginas
                    });
                })
            })




    }





    componentDidMount() {

        GetPontoCadastro(this.props).then(x => {
            if (x != undefined) {
                const { pontos, icones, paginas } = x
                this.setState({
                    // pontos,
                    icones,
                    paginas
                });
            }

        })

    }

    render() {


        return <div className='container-fluid' style={{ backgroundColor: 'white', height: window.innerHeight, margin: 'auto' }}>
            <div className='row'>
                <div className='col-12'>



                    <form id="formulario" onSubmit={(event) => {
                        event.preventDefault();
                        const formData = new FormData(document.getElementById('formulario'))
                        const arr = {}
                        for (var pair of formData.entries()) {
                            arr[pair[0]] = pair[1]
                        }
                        this.envia(arr)
                    }}>


                        <div className='row'>
                            <div className='col-12'>
                                <h1>Cadastro Ponto</h1>
                            </div>
                        </div>

                        <div className='row'>
                            <div className='col-8 form-group '>
                                <input type="number" id="IdPonto" placeholder="IdPonto" hidden />

                                <input className='form-control' type="text" id="NomePonto" name='NomePonto' placeholder="NomePonto" />
                            </div>
                            <div className='col-md-2 form-group '>
                                <input className='form-control' type="text" id="LatitudePonto" name="LatitudePonto" placeholder="LatitudePonto" />
                            </div>

                            <div className='col-md-2 form-group '>
                                <input className='form-control' type="text" id="LongitudePonto" name="LongitudePonto" placeholder="LongitudePonto" />
                            </div>
                        </div>


                        <div className='row'>
                            <div className='col-6'>
                                <label className='form-label'>icones</label>
                                <input list="IconeIds" className='form-control' name="IconeId" id="IconeId" />

                                <datalist id="IconeIds" hidden className='form-control' onChange={data => console.log(data.target.selectedIndex)}>
                                    {this.state.icones.map(data => {
                                        return <option
                                            key={data.idIcone}
                                            value={data.idIcone}>
                                            {data.nomeIcone}
                                        </option>
                                    })}
                                </datalist>
                            </div>

                            <div className='col-4 form-group'>
                                <label className='form-label'>paginas</label>

                                <input list="PaginaIds" className='form-control' name="PaginaId" id="PaginaId" />

                                <datalist id="PaginaIds" hidden className='form-control' onChange={data => console.log(data.target.selectedIndex)}>
                                    {this.state.paginas.map(data => {
                                        return <option
                                            key={data.idPagina}
                                            value={data.idPagina}>
                                            {data.nomePagina}
                                        </option>
                                    })}
                                </datalist>
                            </div>

                            <div className='col-2'>
                                <Link to="/CadastroPagina">

                                    <button
                                        className='btn btn-primary'
                                        style={{
                                            marginTop: '30px'
                                        }}>
                                        Cadastrar Pagina
                                    </button>
                                </Link>
                            </div>
                        </div>


                        <div className='row' style={{ marginTop: '30px' }}>
                            <div className='col-12'>
                                <button className='btn btn-success' type="submit">Salvar</button>
                            </div>
                        </div>
                    </form>

                </div>
            </div>
        </div>;
    }
}

export default CadastroPonto;