import React, { Component } from 'react';
import { Link } from "react-router-dom";

const GetPontoCadastro = (props) => {

    return fetch(`${process.env['REACT_APP_BASE_URL']}/CadastroPonto/LoadPontoCadastro`, {
        method: 'get',
        headers: new Headers({
            'content-type': 'application/json',
            'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
        })
    }).then(x => {
        if (x.status === 200) return x.json()
        if (x.status === 401) props.history.push('/Login')
        return
    })

}



class ListaPontos extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        pontos: []
    }

    componentDidMount() {
        GetPontoCadastro(this.props).then(x => {
            if (x != undefined) {
                const { pontos, icones, paginas } = x
                this.setState({
                    pontos,
                    icones,
                    // paginas
                });
            }

        })
    }
    render() {
        return (<div style={{ height: '100%', backgroundColor: 'white' }}>
            <Link to="/CadastroPonto">

                <button
                    className='btn btn-primary'
                    style={{
                        marginTop: '30px'
                    }}>
                    Cadastrar Ponto
                </button>
            </Link>


            <table class="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">Nome Ponto</th>
                        <th scope="col">Latitude</th>
                        <th scope="col">Longitude</th>
                        <th scope="col">Icone</th>
                        <th>Editar</th>
                        <th>Ir Mapa</th>
                    </tr>
                </thead>
                <tbody>
                    {this.state.pontos.map(x =>
                        <tr key={x.idPonto} onClick={data => {

                            const g = this.state.pontos.filter(xg => xg.idPonto === x.idPonto)
                            console.log(g[0])
                            // this.props.history.push(`/?lat=${x.latitudePonto}&long=${x.longitudePonto}&zooml=15`)
                        }}>


                            <td>{x.nomePonto}</td>
                            <td>{x.latitudePonto}</td>
                            <td>{x.longitudePonto}</td>
                            <td><img
                                // width='10px'
                                // height={27}
                                src={this.state.icones.filter(xg => xg.idIcone === x.iconeId)[0].linkIcone.indexOf('http') === -1 ?
                                    `${process.env['REACT_APP_BASE_URL2']}${this.state.icones.filter(xg => xg.idIcone === x.iconeId)[0].linkIcone}` :
                                    this.state.icones.filter(xg => xg.idIcone === x.iconeId)[0].linkIcone}
                            /></td>
                            <td>
                                <Link to={`/EditarPonto/${x.idPonto}`}>

                                    <button
                                        className='btn btn-primary'
                                        style={{
                                            marginTop: '30px'
                                        }}>
                                        Editar
                                    </button>
                                </Link>


                            </td>
                            <td>
                                <button
                                    className='btn btn-primary'
                                    onClick={() => this.props.history.push(`/?lat=${x.latitudePonto}&long=${x.longitudePonto}&zooml=15`)}
                                    style={{
                                        marginTop: '30px'
                                    }}>
                                    Ir
                                </button>
                            </td>
                            {/* <td>{this.state.icones.filter(xg => xg.idIcone === x.iconeId)[0].linkIcone}</td> */}




                        </tr>)}




                </tbody>
            </table>




        </div>

        );
    }
}

export default ListaPontos;